import React, {Component} from 'react';
import './TodoItem.css';
import {uploadTodos} from "./leanCloud"

// Component TodoItem
export default class TodoItem extends Component {
    render() {
        return (
            <div className='TodoItem'>
                {/*<App/>checkbox*/}
                <input type="checkbox"
                       checked={this.props.todo.status}
                       onChange={this.toggle.bind(this)}/>
                {/*<App/>TodoInputspan*/}
                <span className="title">{this.props.todo.title}</span>
                <button onClick={this.delete.bind(this)}></button>
            </div>
        )
    }

    //  <App/> 
    delete(e) {
        this.props.onDelete(e, this.props.todo)
    }

    //  <App/> checkbox
    toggle(e) {
        this.props.onToggle(e, this.props.todo)
    }
}
export function error(error){
    switch(error.code){
        case 210:
            alert('')
            break
        case 211:
            alert('')
            break
        case 202:
            alert('')
            break
        case 201:
            alert('')
            break
        case 200:
            alert('')
            break
        case 217:
            alert('')
            break
        case 218:
            alert('')
            break
        case 502:
            alert('')
            break
        default:
            alert(error)
            break
    }
}


